import {createApp} from 'vue'
import {createRouter,createWebHistory} from 'vue-router'
import App from './App.vue'

import Home from "./components/Home"
import Kinematics from "./components/Kinematics"
import Vectors from "./components/Vectors"
import Dynamics from "./components/Dynamics"

const router=createRouter({
	history:createWebHistory(),
	routes:[
	{path:"/vuepages3/",component:Home},
	{path:"/vuepages3/kinematics",component:Kinematics},
	{path:"/vuepages3/vectors",component:Vectors},
	{path:"/vuepages3/dynamics",component:Dynamics}
	]
})

const app=createApp(App)
app.use(router)
app.mount('#app')
